<template>
	<view>
		<view class="top_hr"></view>
		<view style="position: fixed;top: 0;width: 100%;">
			<headers title="好友页面" :leftIcon="true" />
		</view>
		
		<view class="user">
			<view class="icon">
				<view class="icon_left">
					<image :src="obj.avatar" mode="widthFix"></image>
					<text>{{obj.username}}</text>
				</view>
				<view class="fn">
					<text @click="Delete" style="background: red;">删除好友</text>
					<text @click="sendMsg">发送信息</text>
					<!-- <text @click="attention" v-if="obj.is_attentionuser==='0'&&userid!==id*1">关注</text>
					<text class="red" @click="unAttention" v-if="obj.is_attentionuser==='1'">取消关注</text>
					<text @click="btn" v-if="obj.is_friend==='0'&&userid!==id*1">添加好友</text>
					<text class="red" @click="deleteFirend" v-if="obj.is_friend==='1'">删除好友</text> -->
				</view>
				<!-- <view class="btn" @click="btn"><text>添加为好友</text></view> -->
			</view>
			<view class="con">
				<view class=""><text>关注用户</text><text>{{obj.attention_num}}</text></view>
				<view class=""><text>粉丝</text><text>{{obj.fans_num}}</text></view>
				<view class=""><text>动态文章</text><text>{{obj.dynamic_num}}</text></view>
				<view class=""><text>视频</text><text>{{obj.video_num}}</text></view>
			</view>
		</view>
	</view>
</template>

<script>
	import headers from '@/components/header.vue';
	import popup from '@/components/uni-popup/uni-popup.vue'
	export default {
		components:{headers,popup},
		data() {
			return {
				token:'',
				id:'',
				obj:{},
				str:''
			};
		},
		onLoad(e) {
			console.log(e)
			let {token,user_id} = uni.getStorageSync('userinfo');
			this.token = token;
			this.userid = user_id;
			let str = JSON.parse(e.data);
			this.str = e.data;
			console.log(str)
			this.id = str.userid;
			this.getMsg();
			console.log(this.userid,this.id)
		},
		methods:{
			async getMsg(id){
				let obj = {token:this.token,text:this.id};
				let res = await this.$Post('/api/Userfriend/finduser',obj);
				if(res.data.code===1){
					this.obj = res.data.data;
				}
				console.log(res); 
			},
			async btn(){
				let {token} = uni.getStorageSync('userinfo');
				let obj = {token,userid:this.obj.userid};
				let data = await this.$Post('/api/Userfriend/addfirend',obj);
				this.$Toast(data.data.msg);
				if(res.data.code===1){
					this.getMsg(this.id)
				}
				// console.log(this.obj);
			},//添加好友
			async deleteFirend(){
				let obj = {token:this.token,userid:this.obj.userid};
				let data = await this.$Post('/api/Userfriend/delfirend',obj);
				this.$Toast(data.data.msg);
				if(res.data.code===1){
					this.getMsg(this.id)
				}
			},//删除好友
			async attention(){
				let obj = {token:this.token,userid:this.id};
				let res = await this.$Post('/api/Userfriend/AddAttentionUser',obj);
				this.$Toast(res.data.msg);
				if(res.data.code===1){
					this.getMsg(this.id);
				}
			},//关注
			async unAttention(){
				let obj = {token:this.token,userid:this.id};
				let res = await this.$Post('/api/Userfriend/DelAttentionUser',obj);
				this.$Toast(res.data.msg);
				if(res.data.code===1){
					this.getMsg(this.id);
				}
			},//取消关注
			sendMsg(){
				uni.navigateTo({
					url:`./firendRoom?data=${this.str}`
				})
			},
			async Delete(){
				let obj = {token:this.token,userid:this.id};
				let res = await this.$Post('/api/Userfriend/delfirend',obj);
				this.$Toast(res.data.msg);
				if(res.data.code===1){
					setTimeout(()=>{
						uni.navigateBack({
							delta:1
						})
					},500)
				}
			},//删除好友
		}
	}
</script>

<style lang="scss" scoped>
	.fn{
		text{
			margin:  15rpx;
			font-size: 26rpx;
			color: white;
			background: #34BEFF;
			box-sizing: border-box;
			padding: 15rpx 20rpx;
			border-radius: 10rpx;
		}
		.red{
			background: red;
		}
	}
.top_hr{
		width: 100%;
		height: 160rpx;
	}
	.user{
		width: 100%;
		box-sizing: border-box;
		padding: 40rpx 20rpx;
		.icon{
			display: flex;
			// flex-direction: row;
			justify-content: space-between;
			align-items: center;
			font-size: 30rpx;
			margin-bottom: 20rpx;
			.icon_left{
				display: flex;
				flex-direction: row;
				align-items: center;
				image{
					width: 80rpx;
					height: 80rpx;
					border-radius: 50%;
					margin-right: 15rpx;
				}
			}
			
		}
		.con{
			width: 100%;
			display: flex;
			justify-content: space-between;
			align-items: center;
			font-size: 30rpx;
		}
	}
</style>
